import React, { useEffect, useState, useContext, useCallback, useRef } from 'react';
import { Row, Col } from 'reactstrap';
import axios from 'axios';
import { Context } from './../../Job/index';

// Images
import JobItem from 'src/components/JobItem';
import { useMemo } from 'react';



const JobListingItem = (props) => {
    const [services, setServices] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [postsPerPage, setPostPerPage] = useState(12);
    const [currentPage, setCurrentPage] = useState(1);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    const context = useContext(Context);

	useEffect(() => context.func(paginate), [context]);

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await axios.get('https://www.togedoortestgo.site/graphql/services');
				setServices(response.data);
				// context.getServices(response.data);
            } catch (error) {
                console.log(error.response);
                setIsLoading(true);
            }
        }
        fetchData();
    }, []);

	useEffect(() => {
        // console.log(props.services);
        context.getServices(props.services);
        setServices(props.services);
    }, [props.services]);

    const indexOfLastPost = currentPage * postsPerPage;
    const indexOfFirstPost = indexOfLastPost - postsPerPage;

    const currentPosts = services
        .sort((a, b) => (a.createdAt < b.createdAt ? 1 : -1))
        .slice(indexOfFirstPost, indexOfLastPost);

    const { grid } = props;
    return (
        <>
            {grid ? (
                <Row>
                    {currentPosts &&
                        currentPosts.map((item, i) => (
                            <Col md={6} key={i + item}>
                                <JobItem
                                    {...item}
                                    grid={grid}
                                    firstColumn='col-md-12 col-sm-12 col-xs-12 p-0'
                                    secondColumn='col-md-12 col-sm-12 col-xs-12 p-0'
                                    textPosition='text-left '
                                />
                            </Col>
                        ))}
                </Row>
            ) : (
                <Row>
                    {currentPosts &&
                        currentPosts.map((item, i) => (
                            <Col md={12} key={i + item}>
                                <JobItem
                                    {...item}
                                    grid={grid}
                                    firstColumn='col-md-8 col-sm-12 col-xs-12'
                                    secondColumn='col-md-4 col-sm-12 col-xs-12'
                                    textPosition='text-left text-md-right'
                                />
                            </Col>
                        ))}
                </Row>
            )}
        </>
    );
};

export default JobListingItem;
