import React, { useRef } from 'react';
import { Button } from 'reactstrap';

const FilterBox = (props) => {
  const { subject, count, i, viewByCategory } = props;
  const ref = useRef();

  const setIdFunction = (e) => viewByCategory(i);

  return (
    <div className='custom-control custom-checkbox mb-2 py-0 pr-0 pl-0 filter-box ml-1 mr-0'>
      <Button
        value={subject}
        innerRef={ref}
        className='btn btn-light label ml-0 py-2 py-1 border-0'
        id={subject}
        onClick={setIdFunction}>
        <strong>
          {subject}
          <span className='ml-1 count'>({count})</span>
        </strong>
      </Button>
    </div>
  );
};

export default FilterBox;
