import React, { useState, useEffect, useRef, useContext, useCallback } from 'react';
import { Col, Container, Form, Row, FormGroup, Input, Button } from 'reactstrap';
import axios from 'axios';
import { Link } from 'react-router-dom';
import DashboardCard from 'src/components/DashboardCard';
import FilterBox from 'src/components/FilterBox';
import { Context } from './../../../Job/index';

const instance = axios.create({
    baseURL: 'https://www.togedoortestgo.site/graphql',
});

function concatWithDash(string) {
    return string.split(' ').join('-');
}

const JobCategory = () => {
    const [posts, setPosts] = useState([]);
    const [categories, setCategories] = useState([]);
    const [categoryName, setCategoryName] = useState('Agriculture & Natural Resources');
    const [subCategories, setSubCategories] = useState([]);
    const [searchResults, setSearchResults] = useState([]);
    const [countedPosts, setCountedPosts] = useState([]);
    const [jobCategory, setJobCategory] = useState([]);
    const context = useContext(Context);

    // console.log(context);
    /** set posts with @callback useFetch */
    const useFetch = useCallback(async (url, callbacks, categoryName) => {
        try {
            const { data } = await instance.get(categoryName ? `categories/name/${categoryName}` : url);

            callbacks.forEach((callback) => callback(categoryName ? data.subcategories : data));
        } catch (e) {
            console.log(e);
        }
    }, []);

    useEffect(() => {
        useFetch('services', [setPosts, setSearchResults]);
        useFetch('categories', [setCategories]);
    }, []);

    useEffect(() => {
        useFetch('categoryName', [setSubCategories], concatWithDash(categoryName));
    }, [categoryName]);

    useEffect(() => {
        setCountedPosts(
            categories.map((category) =>
                posts.filter((post) => post.categoryName.split('-').join(' ') === category.name)
            )
        );
        setJobCategory(categories.map((item) => ({ subject: item.name, count: 0 })));
    }, [categories]);

    useEffect(() => {
        setJobCategory(
            categories.map((item, i) => {
                if (countedPosts[i]) return { subject: item.name, count: countedPosts[i].length };
            })
        );
    }, [countedPosts]);

    const viewByCategory = (id) => {
        context.showByCategory(countedPosts[id]);
    };

    return (
        <DashboardCard title='Projects by category' className='mb-5'>
            {Array.isArray(jobCategory) &&
                jobCategory.map((item, i) => (
                    <FilterBox key={i} i={i} viewByCategory={() => viewByCategory(i)} {...item} />
                ))}
            {/* {jobCategory.length >= 7 && (
                <Link to='#' className='text-primary ml-2'>
                    View all categories{' '}
                </Link>
            )} */}
        </DashboardCard>
    );
};

export default JobCategory;
