import React, { useState,useEffect, useCallback, createContext, useMemo } from 'react';
import { Container, Row, Col } from 'reactstrap';

import ListingSidebar from './ListingSidebar';
import ListingCategory from './ListingCategory';
import Pagination from 'src/components/Pagination';

export const Context = createContext();

const Job = () => {
    const [pageNumber, setPageNumber] = useState(1);
    const [services, setServices] = useState([]);
    const func = useMemo(() => (f) => f(pageNumber), [pageNumber]);

	const showByCategory = (data) => {
		setServices(data);
	};

	const getServices = (services) => {
		setServices(services);
	}

	const sendFunctions = {
		func, showByCategory, getServices
	}
    return (
        <Container className='pt-5'>
            <Row>
                <Context.Provider value={sendFunctions}>
                    <Col
                        lg={{ size: 3, order: 1 }}
                        md={{ size: 12, order: 2 }}
                        sm={{ size: 12, order: 2 }}
                        xs={{ size: 12, order: 2 }}>
                        <ListingSidebar />
                    </Col>
                    <Col
                        lg={{ size: 9, order: 2 }}
                        md={{ size: 12, order: 1 }}
                        sm={{ size: 12, order: 1 }}
                        xs={{ size: 12, order: 1 }}>
                        <ListingCategory services={services} getServices={getServices} /> {/* ==> JobListingItem */}
                        <Pagination setPageNumber={setPageNumber} />
                    </Col>
                </Context.Provider>
            </Row>
        </Container>
    );
};

export default Job;
